import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { RestangularModule } from 'ngx-restangular/dist/esm/src';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { ChartsModule } from 'ng2-charts/ng2-charts';
import { IonicStorageModule } from '@ionic/storage';
import { HttpClientModule } from '@angular/common/http';


import { environment } from '../environments/environment';


import { MyApp } from './app.component';

import { TabsPage } from '../pages/tabs/tabs';
import { AboutPage } from '../pages/about/about';
import { ChatPage } from '../pages/chat/chat';
import { ChatdetailPage, ChatDetailPopoverPage } from '../pages/chatdetail/chatdetail';
import { ConfirmationPage } from '../pages/confirmation/confirmation';
import { DeliveryaddressPage } from '../pages/deliveryaddress/deliveryaddress';
import { DetailsPage, DetailPopoverPage } from '../pages/details/details';
import { FavoritesPage } from '../pages/favorites/favorites';
import { HomePage } from '../pages/home/home';
import { LoginPage } from '../pages/login/login';
import { MakepaymentPage } from '../pages/makepayment/makepayment';
import { MyaccountPage } from '../pages/myaccount/myaccount';
import { MyprofilePage } from '../pages/myprofile/myprofile';
import { MyratingPage } from '../pages/myrating/myrating';
import { MyaddressPage } from '../pages/myaddress/myaddress';
import { MyaddressEntryPage } from '../pages/myaddress-entry/myaddress-entry';
import { MybankcardsPage } from '../pages/mybankcards/mybankcards';
import { MybankcardsEntryPage } from '../pages/mybankcards-entry/mybankcards-entry';
import { MywalletPage } from '../pages/mywallet/mywallet';
import { OrderdetailsPage } from '../pages/orderdetails/orderdetails';
import { PaymentmethodPage } from '../pages/paymentmethod/paymentmethod';
import { PaymentoptionPage } from '../pages/paymentoption/paymentoption';
import { ProductcommentPage } from '../pages/productcomment/productcomment';
import { ProductratingsPage } from '../pages/productratings/productratings';
import { SearchPage } from '../pages/search/search';
import { SearchresultPage } from '../pages/searchresult/searchresult';
import { SettingsPage } from '../pages/settings/settings';
import { ShippingoptionPage } from '../pages/shippingoption/shippingoption';
import { ShoppingcartPage } from '../pages/shoppingcart/shoppingcart';
import { ShopprofilePage } from '../pages/shopprofile/shopprofile';
import { SignupPage } from '../pages/signup/signup';
import { ForgotPage } from '../pages/forgot/forgot';
import { UserprofilePage } from '../pages/userprofile/userprofile';
import { NotificationPage } from '../pages/notification/notification';
import { AccountsettingsPage } from '../pages/accountsettings/accountsettings';

import { EmojiPickerComponent } from '../components/emoji-picker/emoji-picker';

import { RelativeTimePipe } from '../pipes/relative-time/relative-time';


import { ServiceProvider } from '../providers/service/service';
import { AuthProvider } from '../providers/service/authentication';
import { EmojiProvider } from '../providers/service/emojis';
import { ChatService } from '../providers/service/chat';


import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { HTTP } from '@ionic-native/http';


export function RestangularConfigFactory(RestangularProvider) {
    RestangularProvider.setBaseUrl(environment.api_url);
    RestangularProvider.setPlainByDefault(true);
    RestangularProvider.setFullResponse(true);
}

@NgModule({
    declarations: [
        MyApp,
        TabsPage,
        AboutPage,
        ChatPage, ChatdetailPage, ChatDetailPopoverPage,
        ConfirmationPage,
        DeliveryaddressPage,
        DetailsPage, DetailPopoverPage,
        FavoritesPage,
        HomePage,
        LoginPage,
        MakepaymentPage,
        MyaccountPage,
        MyprofilePage,
        MyratingPage,
        OrderdetailsPage,
        PaymentmethodPage,
        PaymentoptionPage,
        ProductcommentPage,
        ProductratingsPage,
        SearchPage,
        SearchresultPage,
        SettingsPage,
        ShippingoptionPage,
        ShoppingcartPage,
        ShopprofilePage,
        SignupPage,
        UserprofilePage,
        NotificationPage,
        AccountsettingsPage,
        ForgotPage,
        MyaddressPage,
        MyaddressEntryPage,
        MybankcardsPage,
        MywalletPage,
        MybankcardsEntryPage,
        EmojiPickerComponent,RelativeTimePipe
    ],
    imports: [
        BrowserModule,
        CommonModule,
        IonicModule.forRoot(MyApp, {
            backButtonText: '',
            iconMode: 'ios',
            mode: 'ios'
        }),
        RestangularModule.forRoot(RestangularConfigFactory),
        FormsModule,
        IonicStorageModule.forRoot(),
        ChartsModule,
        HttpClientModule
    ],
    bootstrap: [IonicApp],
    entryComponents: [
        MyApp,
        TabsPage,
        AboutPage,
        ChatPage, ChatdetailPage, ChatDetailPopoverPage,
        ConfirmationPage,
        DeliveryaddressPage,
        DetailsPage, DetailPopoverPage,
        FavoritesPage,
        HomePage,
        LoginPage,
        MakepaymentPage,
        MyaccountPage,
        MyprofilePage,
        MyratingPage,
        OrderdetailsPage,
        PaymentmethodPage,
        PaymentoptionPage,
        ProductcommentPage,
        ProductratingsPage,
        SearchPage,
        SearchresultPage,
        SettingsPage,
        ShippingoptionPage,
        ShoppingcartPage,
        ShopprofilePage,
        SignupPage,
        UserprofilePage,
        NotificationPage,
        AccountsettingsPage,
        ForgotPage,
        MyaddressPage,
        MyaddressEntryPage,
        MybankcardsPage,
        MywalletPage,
        MybankcardsEntryPage,
        EmojiPickerComponent
    ],
    providers: [
        StatusBar,
        SplashScreen,
        HTTP,
        { provide: ErrorHandler, useClass: IonicErrorHandler },
        AuthProvider,
        EmojiProvider,
        ChatService
    ]
})
export class AppModule { }



