import { Component, ViewChild } from '@angular/core';
import { DatePipe } from '@angular/common';
import { NavController, NavParams, LoadingController, AlertController, ModalController, ViewController, PopoverController } from 'ionic-angular';
import * as _ from 'lodash';
import { Storage } from '@ionic/storage';

import { ShoppingcartPage } from '../../pages/shoppingcart/shoppingcart';
import { ChatdetailPage } from '../../pages/chatdetail/chatdetail';
import { ProductratingsPage } from '../../pages/productratings/productratings';
import { ShopprofilePage } from '../../pages/shopprofile/shopprofile';
import { ProductcommentPage } from '../../pages/productcomment/productcomment';


@Component({
  template: `
    <ion-list>
      <ion-item (click)="viewChat()"><ion-icon ios="ios-chatbubbles-outline" md="ios-chatbubbles-outline" item-start></ion-icon>Chat Now</ion-item>
      <ion-item (click)="close()"><ion-icon ios="ios-share-outline" md="ios-share-outline" item-start></ion-icon>Share</ion-item>
      <ion-item (click)="close()"><ion-icon ios="ios-home-outline" md="ios-home-outline" item-start></ion-icon>Back to Home</ion-item>
      <ion-item (click)="close()"><ion-icon ios="ios-information-circle-outline" md="ios-information-circle-outline" item-start></ion-icon>Report this product</ion-item>
    </ion-list>
  `
})
export class DetailPopoverPage {
  constructor(public viewCtrl: ViewController, public navCtrl: NavController) { }

  close() {
    this.viewCtrl.dismiss();
  }

  viewChat() {
    // TODO: Check if Authenticated
    this.navCtrl.push(ChatdetailPage);
    this.viewCtrl.dismiss();
  }
}
@Component({
  selector: 'page-details',
  templateUrl: 'details.html',
})
export class DetailsPage {
  
  constructor(public navCtrl: NavController, public navParams: NavParams, public loadingCtrl: LoadingController, public storage: Storage, public alertCtrl: AlertController, public modalCtrl: ModalController, public popoverCtrl: PopoverController) {
    
  }

  ionViewDidLoad() {
    
  }

  doRefresh(refresher) {
    setTimeout(()=>{
      refresher.complete();
    },1000);
  }

  likeItem() {
    
  }

  presentPopover(myEvent) {
    let popover = this.popoverCtrl.create(DetailPopoverPage);
    popover.present({
      ev: myEvent
    });
  }

  viewCart(){
    // TODO: Check if Authenticated
    let modal = this.modalCtrl.create(ShoppingcartPage);
    modal.present();
  }

  viewProductRating(){
    this.navCtrl.push(ProductratingsPage);
  }

  viewShopProfile(){
    this.navCtrl.push(ShopprofilePage);
  }

  viewProductComment(){
    this.navCtrl.push(ProductcommentPage);
  }
}



